<template>
    <form class="row" @submit.prevent="submit()" >
        
        <div class="col-lg-12">
            <div class="card">
                
                <div class="card-body" >
                    <div class="row">
                        <div class="col-md-12">
                            <h3>Cadastro de Empresas <span v-if="form.id">: {{form.apelido}}</span> </h3>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-md-3">
                            <div class="nav flex-column nav-pills" id="v-pills-tab" role="tablist" aria-orientation="vertical">
                                <a class="nav-link mb-2 active" id="v-pills-home-tab" data-toggle="pill" href="#v-pills-home" role="tab" aria-controls="v-pills-home" aria-selected="false">Cadastro</a>
                            </div>
                        </div>
                        <div class="col-md-9">

                            <div class="tab-content text-muted mt-4 mt-md-0" id="v-pills-tabContent">
                                <div class="tab-pane fade active show" id="v-pills-home" role="tabpanel" aria-labelledby="v-pills-home-tab">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <h4>Dados Cadastrais</h4>
                                        </div>
                                    </div>
                                    <hr>
                                    <div class="row">
                                        <label class="col-sm-3 col-form-label" v-show="form.id">Id</label>
                                        <div class="col-sm-9" v-show="form.id">
                                            <input-componente
                                                :disabled="true"
                                                v-model="form.id"                            
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Nome</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                v-model="form.nome"
                                                :validator="$v.form.nome"
                                                @blur="$v.form.nome.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Empresa:</label>
                                        <div class="col-sm-9">
                                            <container-componente :validator="$v.form.empresa_id">
                                                <vue-select style="height: calc(1.5em + 0.94rem - 6px)"  placeholder="Comece a digitar para buscar"
                                                        :options="empresas" label="apelido"
                                                        @input=" $v.form.empresa_id.$touch; form.empresa_id = empresa.id;"
                                                        @search="buscarEmpresasDebounce"
                                                        v-model="empresa">
                                                        <template slot="option" slot-scope="option">
                                                            {{ option.apelido }} / {{ option.razao_social}}
                                                        </template>
                                                        <div slot="no-options">Nenhum item encontrado!</div>
                                                        <div class="spinner" v-show="buscandoEmpresas">Buscando...</div>
                                                    </vue-select>
                                            </container-componente> 
                                        </div>

                                         <label class="col-sm-3 col-form-label">Cep:</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                :mask="['#####-###']"
                                                v-model="form.cep"
                                                :validator="$v.form.cep"
                                                @blur="$v.form.cep.$touch; buscarCep()"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Tipo:</label>
                                        <div class="col-sm-9">
                                            <container-componente :validator="$v.form.endereco_tipo">
                                                <select class="form-control"  v-model="form.endereco_tipo">
                                                    <option :value="'Alameda'">Alameda</option>
                                                    <option :value="'Avenida'">Avenida</option>
                                                    <option :value="'Estrada'">Estrada</option>
                                                    <option :value="'Morro'">Morro</option>
                                                    <option :value="'Parque'">Parque</option>
                                                    <option :value="'Pátio'">Pátio</option>
                                                    <option :value="'Praça'">Praça</option>
                                                    <option :value="'Rodovia'">Rodovia</option>
                                                    <option :value="'Travessa'">Travessa</option>
                                                    <option :value="'Rua'">Rua</option>

                                                </select>
                                            </container-componente>
                                        </div>

                                        <label class="col-sm-3 col-form-label">Logradouro:</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                v-model="form.logradouro"
                                                :validator="$v.form.logradouro"
                                                @blur="$v.form.logradouro.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Número:</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                v-model="form.numero"
                                                :validator="$v.form.numero"
                                                @blur="$v.form.numero.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Bairro:</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                v-model="form.bairro"
                                                :validator="$v.form.bairro"
                                                @blur="$v.form.bairro.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Cidade:</label>
                                        <div class="col-sm-9">
                                            <container-componente :validator="$v.form.cidade_id">
                                                <vue-select style="height: calc(1.5em + 0.94rem - 6px)"  placeholder="Comece a digitar para buscar"
                                                        :options="cidades" label="nome"
                                                        @input="form.cidade_id=cidade.id"
                                                        @blur="$v.form.cidade_id.$touch" @search="buscarCidadesDebounce"
                                                        v-model="cidade">
                                                        <template slot="option" slot-scope="option">
                                                            {{ option.nome }} / {{ option.sigla}}
                                                        </template>
                                                        <div slot="no-options">Nenhum item encontrado!</div>
                                                        <div class="spinner" v-show="buscandoCidade">Buscando...</div>
                                                    </vue-select>
                                            </container-componente> 
                                        </div>

                                        <label class="col-sm-3 col-form-label">Complemento:</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                v-model="form.complemento"
                                                :validator="$v.form.complemento"
                                                @blur="$v.form.complemento.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Email</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                                autofocus="true"
                                                v-model="form.email"
                                                :validator="$v.form.email"
                                                @blur="$v.form.email.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Telefone</label>
                                        <div class="col-sm-9">
                                            <input-componente
                                            :mask="['(##) ####-####', '(##) #####-####']"
                                                v-model="form.fone"
                                                :validator="$v.form.fone"
                                                @blur="$v.form.fone.$touch"
                                                />
                                        </div>

                                        <label class="col-sm-3 col-form-label">Ativo</label>
                                        <div class="col-sm-5">
                                            <div class="custom-control custom-checkbox" >
                                                <input type="checkbox" class="custom-control-input" id="customCheckAtivo" v-model="form.ativo" >
                                                <label class="custom-control-label" for="customCheckAtivo">Ativa ({{form.ativo ? 'Sim' : 'Não'}})</label>
                                            </div>
                                        </div>

                                    </div>

                                </div>
                            </div>
                            
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12 text-center" v-if="erros || sucesso">
                            <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                            <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                        </div>
                        <div class="col-md-12 button-items text-center">

                            <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'estoquesLista'}">
                                <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                            </router-link>

                            <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                                <i class="bx bxs-save d-block font-size-16"></i> Salvar
                            </button>

                        </div>
                    </div>
                     <div class="row">
                        <div class="col-md-12 text-right" v-if="form.id">
                            <log-componente :index="form.id" subject_type="App\Models\Cliente\Estoque"  :subject_id="form.id" ></log-componente>
                        </div>
                     </div>
                </div>
            </div>
        </div>
    </form>
</template>

<script>

import { required, email, maxLength} from "vuelidate/lib/validators";
import { EstoqueService, EmpresaService, EnderecoService } from '../../../services'
import moment from 'moment'
import container from '../../../components/forms/container.vue';

const estoqueService = new EstoqueService();
const empresaService = new EmpresaService();
const enderecoService = new EnderecoService();

export default {
  components: { container },

  data() {
        return {
            form: {
                id: null,
                nome: '',
                empresa_id: null,
                ativo: true,
                cidade_id: '',
                endereco_tipo: '',
                logradouro: '',
                numero: '',
                complemento: '',
                bairro: '',
                cep: '',
                fone: '',
                email: '',             
            },
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
              
            timer: null,
            cidades: [],
            cidade:{},
            buscandoCidade: false,


            
            empresa: null,
            buscandoEmpresas: false,
            empresas: [],

        } ;
    },
    validations: {
        form: {
            nome: { required, maxLength:maxLength(50)},
            ativo: { required },
            cidade_id: { },
            endereco_tipo: {  maxLength:maxLength(20)},
            logradouro: {  maxLength:maxLength(100)},
            numero: {  maxLength:maxLength(10)},
            complemento: { maxLength:maxLength(100)},
            bairro: {  maxLength:maxLength(100)},
            cep: { maxLength:maxLength(10)},
            fone: { maxLength:maxLength(20)},
            email: { email, maxLength:maxLength(150)},
            empresa_id: { required }
        },

    },
    mounted() {
        this.buscarEmpresas();
        if (this.$route.params.id) {
            setTimeout(() => {
                this.buscarObjeto(this.$route.params.id);
            }, 200);
        }       
    },
    methods: {

        submit: function(){

            
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';
            // this.$data.form.empresa_id = this.empresa.id;

            this.$v.form.$touch();
            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            let request = null;
            if(obj.id)    
                request = estoqueService.atualizar(obj);
            else
                request = estoqueService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            estoqueService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    this.roles = response.data.roles;
                    if(response.data.cidade){
                        this.cidades.push( { id: response.data.cidade.id, nome: response.data.cidade.nome } );
                        this.cidade = this.cidades[0];
                    }
                    
                    this.empresa = this.empresas[this.empresas.map(x => x.id).indexOf(response.data.empresa_id)];
                }).catch( erro => this.erros = erro)
        },

        buscarCep: function(){
            enderecoService.buscarEnderecoPorCep(this.form.cep)
                .then((response) => {
                console.log(response);
                if(response.data.logradouro){
                    this.form.logradouro = response.data.logradouro.substr( response.data.logradouro.indexOf(' ')+1 );
                    this.form.endereco_tipo = response.data.logradouro.substr(0, response.data.logradouro.indexOf(' '));
                }else{
                    this.form.logradouro = '';
                    this.form.endereco_tipo = '';
                }
                this.form.bairro = response.data.bairro;
                enderecoService.buscarCidadePorIbge(response.data.ibge)
                    .then((responseCidade) => {
                        console.log(responseCidade.data);
                        this.cidade = { id: responseCidade.data.id, nome:responseCidade.data.nome ,sigla: responseCidade.data.estado.sigla };
                        this.form.cidade_id = this.cidade.id;
                        this.cidades = [];
                    })
                    .catch((errorCity) => {
                        console.log(errorCity);
                    });
                })
                .catch((error) => {});
        },

        buscarCidadesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarCidades(text, loading)
            }, 700);
        },

        buscarCidades: function(text, loading){
            enderecoService.listarCidadesBusca({texto: text})
                .then( response =>  this.cidades = response.data.data )
                .catch( error => console.log(error))
        },

        buscarEmpresasDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarEmpresas(text, loading)
            }, 700);
        },

        buscarEmpresas: function(text, loading){
            this.buscandoEmpresas = true;
            empresaService.listar({texto: text, per_page: 50, id: this.form.id})
                .then( response =>  this.empresas = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoEmpresas = false)
        },

    }
};
</script>
